.topbar {
  height: 46px;
  padding-right: 20px;
  padding-left: 20px;
  border-bottom: 1px solid #eee;
  font-size: 12px;
  width: 100%;
  background-color: #fff;
  transition-duration: 200ms;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
  transform: translateY(0);
  >.container {
    transition-duration: 400ms;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
  }
  .navbar-toggle {
    width: 28px;
    height: 28px;
    float: left;
    border: none;
    padding: 0;
    margin: 12px 20px 10px 0px;
    background-color: transparent;
    cursor: pointer;
    outline: 0;
    .toggle-icon {
      position: relative;
      width: 18px;
      height: 2px;
      display: inline-block;
      background: #999;
      &:before {
        bottom: 12px;
        -webkit-transform: rotate(0);
        -moz-transform: rotate(0);
        transform: rotate(0);
        transition-duration: 400ms;
        transition-property: all;
        transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
        position: absolute;
        left: 0;
        width: 18px;
        height: 2px;
        background: #999;
        content: " ";
      }
      &:after {
        position: absolute;
        left: 0;
        width: 18px;
        height: 2px;
        background: #999;
        content: " ";
        top: -6px;
        -webkit-transform: rotate(0);
        -moz-transform: rotate(0);
        transform: rotate(0);
        transition-duration: 400ms;
        transition-property: all;
        transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
      }
    }
    &:hover {
      .toggle-icon {
        background: $colormain;
        &:before {
          background: $colormain;
        }
        &:after {
          background: $colormain;
        }
      }
    }
    .toggle-icon.is-clicked {
      background: rgba(153,153,153,0);
      &:before {
        bottom: 5px;
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        transform: rotate(45deg);
        transition-duration: 400ms;
        transition-property: all;
        transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
      }
      &:after {
        top: -5px;
        -webkit-transform: rotate(-45deg);
        -moz-transform: rotate(-45deg);
        transform: rotate(-45deg);
        transition-duration: 400ms;
        transition-property: all;
        transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
      }
    }
  }
  .logo {
    display: inline-block;
    padding: 5px 0;
    transition-duration: 400ms;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
    float: left;
  }
  .block-right {
    float: right;
    .user {
      float: right;
      float: right;
      padding: 10px 0 10px 15px;
      color: #333;
      width: 150px;
      display: inline-flex;
      >.avatar {
        width: 26px;
        display: inline-block;
      }
      .name {
        height: 26px;
        line-height: 26px;
        margin-left: 5px;
        display: inline-block;
        font-family: Arial,sans-serif;
        color: #333;
        outline: 0;
        text-decoration: none;
        .ic {
          font-size: 10px;
          display: inline-block;
          vertical-align: middle;
        }
      }
    }
    .button-comments {
      float: right;
      height: 26px;
      line-height: 24px;
      border-radius: 20px;
      background-color: transparent;
      border: 1px solid #ccc;
      display: inline-block;
      padding: 0 10px;
      margin: 10px 0 10px 15px;
      transition-duration: 400ms;
      transition-property: all;
      transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
      color: $colormain;
      text-decoration: none;
      float: right;
      >i.ic {
        font-size: 12px;
        display: inline-block;
        vertical-align: middle;
      }
      .counter {
        font: 400 12px arial;
      }
      &:hover {
        border: 1px solid $colormain;
        color: $colormain;
      }
    }
    .search {
      float: right;
      float: right;
      margin: 8px 10px 10px 10px;
      position: relative;
      form {
        border-radius: 20px;
      }
      .search-input {
        margin: 0;
        font-size: 0.75rem;
        border: 1px solid #dfdfdf;
        height: 34px;
        width: 140px;
        padding: 0 35px 0 15px;
        outline: 0;
        border-radius: 3px;
        -webkit-border-radius: 3px;
        -moz-border-radius: 3px;
        color: #999999;
        &::-webkit-input-placeholder { 
          color: #999999;
        }
        &::-moz-placeholder { 
          color: #999999;
        }
        &:-ms-input-placeholder { 
          color: #999999;
        }
        &:-moz-placeholder { 
          color: #999999;
        }
      }
      .search-btn {
        &:last-child {
          border: 0;
        }
        background-color: transparent;
        font-size: 14px;
        line-height: 1;
        text-align: center;
        padding: 3px 6px;
        white-space: nowrap;
        vertical-align: middle;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
        transition-duration: 400ms;
        transition-property: all;
        transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
        position: absolute;
        top: 0;
        right: 2px;
        >button {
          border: 0;
          background-color: transparent;
          .icon-ns {
            fill: currentColor;
            color: #0064b2;
            width: 20px; height: 20px;
          }
          >i {
            color: #999;
          }
        }
      }
    }
    .social {
      float: right;
      margin: 10px 0 10px 10px;
      float: right;
      .social-item {
        width: 26px;
        height: 26px;
        height: 26px;
        line-height: 26px;
        display: block;
        text-align: center;
        color: #fff;
        font-size: 13px;
        transition-duration: 400ms;
        transition-property: all;
        transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
        .ic {
          color: #fff;
          display: inline-block;
          vertical-align: middle;
        }
        &:hover.si-facebook {
          background-color: #1d2a44;
        }
        &:focus.si-facebook {
          background-color: #1d2a44;
        }
        &:hover.si-twitter {
          background-color: #126db2;
        }
        &:focus.si-twitter {
          background-color: #126db2;
        }
        &:hover.si-google {
          background-color: #802216;
        }
        &:focus.si-google {
          background-color: #802216;
        }
      }
      .social-item.si-facebook {
        background-color: #44619d;
      }
      .social-item.si-twitter {
        background-color: #55acee;
      }
      .social-item.si-google {
        background-color: #dd4b39;
      }
    }
    .button-comments.is-clicked {
      border-color: #ccc;
      color: #ccc;
      &:hover {
        border: 1px solid $colormain;
        color: $colormain;
      }
    }
  }
}
